import './App.css';
import Footer from './Footer';
import { useState } from 'react';
import avatar1 from './images/avatar1.png';
import avatar2 from './images/avatar2.png';
import avatar3 from './images/avatar3.png';
import music from './images/music.mp3';
import sound from './images/sound.wav';
import { motion } from "framer-motion"
import {Switch, Route, Link} from 'react-router-dom'

function App() {
  const [countText, setCountText] = useState('Ready')
  const [showCountDown, setShowCountDown] = useState(true)
  const [count,setCount] = useState(0);
  let [score,setScore] = useState(0);
  const [avatar, setAvatar] = useState(avatar1);
  const [player1Active, setplayer1Active] = useState('');
  const [player2Active, setplayer2Active] = useState('');
  const [player3Active, setplayer3Active] = useState('');
  
  //* let lastHole;
  let timeUp = false;
  let timeLimit = 60000;
  let lastHole = null;
  let countdown;

  
  const avatars =document.querySelectorAll('.avatar');
//* pick the random hole
function pickupRandomHole(){
  const holes = document.querySelectorAll('.game__ground .hole');
  const randomHole = Math.floor(Math.random()*holes.length);
  const hole = holes[randomHole];
  if (hole === lastHole){
      return pickupRandomHole();
  }
  lastHole = hole;
  return hole;
}
//* popout the avatar from hole
function popoutAvatar(){
const time = Math.random() * (800 - 400) + 400;
const hole = pickupRandomHole();
hole.classList.add('up');
setTimeout(function(){
    if (!timeUp) popoutAvatar();
    hole.classList.remove('up');
},time)
}


 // play the game
  function playgame(){
    const countdownBoard = document.querySelector('.countdown-board');
    
    timeUp = false; 
    popoutAvatar();
    setTimeout(()=>{
      timeUp = true;
  }, timeLimit);

  let startCountDown = setInterval(() => {
    countdown -= 1;
    setCount(countdown);
    if (countdown<0){
        countdown = 0;
        clearInterval(startCountDown);
        setCount('Time Up');
        const gameoverScreen = document.querySelector('.gameover-screen');
        gameoverScreen.style.display = 'flex';
        
    }

    if(countdown == 30){
      countdownBoard.style.color = '#f38e4b'
    }

    if(countdown == 15){
      countdownBoard.style.color = '#f34b4b'
    }
}, 1000);
}


  function startGame(){
      setScore(0);
      setShowCountDown(true)
      countdown = timeLimit/1000;
      setCount(countdown);
      setCountText('Ready')
     const countdownBoard = document.querySelector('.countdown-board');
  
    const background_music = document.querySelector('.background_music');
    background_music.pause();
    
    const readyWindow = document.querySelector('.ready-window');
    // readyWindow.style.display = 'flex';
    let count = 0;
    
      const readyCountdown =  setInterval(()=>{
          const readyWindowText = document.querySelector('.ready-window span');
          count = count+1;
          if(count === 1){
           setCountText('3');
          }
          else if(count === 2){
            // readyWindowText.innerText = '2';
            setCountText('2');
          }
          else if(count === 3){
            // readyWindowText.innerText = '1';
            setCountText('1');
          } 
          else if(count === 4){
            setShowCountDown(false)
            clearInterval(readyCountdown);
            playgame();


          }
         
    },1000);
  }
  function hit(){
    var hit_sound = document.querySelector('.hit_sound');
    hit_sound.currentTime = 0;
    hit_sound.play();
    score = score+1;
    setScore(score)
  }

  avatars.forEach(avatar=>{
    avatar.addEventListener('click',hit);
  });
  

function selectionScreen(){
  const background_music = document.querySelector('.background_music');
  background_music.currentTime = 0;
  background_music.play();
}

function avatarSelection(selected_avatar, playerNumber){
  if(playerNumber === 1){
    setplayer1Active('playerActive')
    setplayer2Active('')
    setplayer3Active('')
  }
  else if(playerNumber === 2){
    setplayer1Active('')
    setplayer2Active('playerActive')
    setplayer3Active('')
  }
  else{
    setplayer1Active('')
    setplayer2Active('')
    setplayer3Active('playerActive')
  }
  
  setAvatar(selected_avatar)
}



 
  return (
    <div className="App">
<audio loop className="background_music">
  <source src={ music } type="audio/ogg"/>
Your browser does not support the audio element.
</audio>

<video className="hit_sound">
  <source src={ sound } type="video/wav"/>
  <source src={ sound } type="video/ogg"/>
  Your browser does not support the video tag.
</video>
           
<Switch>
  <Route path="/select-avatar">
  <div className="players" 
 >
              <motion.h1  initial={{y:-100}} animate={{y:0}} 
  transition={{type:"spring", duration: 0.4, stiffness:180}}>Select Avatar</motion.h1>
                  <ul className="players__list">
                    <motion.li 
                    className = {`${setplayer1Active? player1Active:''}`}
                    initial={{scale:0}} 
                    animate={{scale:1}}  
                    transition={{type:"spring", duration: 0.4,delay:0.8, stiffness:180}}
                    onClick={()=> avatarSelection(avatar1,1)} data-select = '1'>
                        <img src={avatar1} alt=""/>
                    </motion.li>
             
                      <motion.li 
                       className = {`${setplayer2Active? player2Active:''}`}
                      initial={{scale:0}} 
                       animate={{scale:1}}  
                       transition={{type:"spring", duration: 0.4,delay:1.2, stiffness:180}}
                        onClick={()=>avatarSelection(avatar2, 2)} data-select = '2'>
                          <img src={avatar2} alt=""/>
                      </motion.li>
               
           
                      <motion.li
                       className = {`${setplayer3Active? player3Active:''}`}
                      initial={{scale:0}} 
                      animate={{scale:1}}  
                      transition={{type:"spring", duration: 0.4,delay:1.6, stiffness:180}}
                        onClick={()=>avatarSelection(avatar3,3)} data-select = '3'>
                         <img src={avatar3} alt=""/>
                      </motion.li>
                  
                  
                     <motion.li
                      initial={{scale:0}} 
                      animate={{scale:1}}  
                      transition={{type:"spring", duration: 0.4,delay:2, stiffness:180}}
                     ></motion.li>
                    
                  </ul>
                  <Link to="/start-game">
                  <motion.button
                   initial={{opacity:0}} 
                   animate={{opacity:1}}  
                   transition={{type:"tween", duration: 1,delay:2.5}}
                   className="continue-btn" onClick={startGame}>Continue</motion.button>
                   </Link>
</div>
     </Route>
 
        <Route exact path="/">
         <div className="intro-window">
              <motion.h1 initial={{y:-100}} animate={{y:0}}>Smash It</motion.h1>
            <Link to="/select-avatar"> <button className="start-btn" onClick={selectionScreen}>START</button></Link> 
            </div>
        </Route>
           
      <Route path="/start-game">
      <div className="game"> 
      {
      showCountDown && (
        <div className="ready-window">
              <span>{countText}</span>
            </div>
      )
       }
            
            <motion.div 
            className="countdown-board"
            initial={{y:-100}} 
            animate={{y:0}}  
            transition={{type:"spring", duration: 0.8,delay:4, stiffness:100}}
            >
              countdown: <span className="countdown">{count}</span></motion.div>
            <div className="game__ground">
              <div className="hole hole1">
                  <div className="avatar sm">
                    <img src={avatar} alt=""/>
                  </div>
              </div>

              <div className="hole hole2">
                  <div className="avatar sm">
                  <img src={avatar} alt=""/>
                  </div>
              </div>

              <div className="hole hole3">
                  <div className="avatar sm">
                  <img src={avatar} alt=""/>
                  </div>
              </div>

              <div className="hole hole4">
                  <div className="avatar lg">
                  <img src={avatar} alt=""/>
                  </div>
              </div>

              <div className="hole hole5">
                  <div className="avatar lg">
                  <img src={avatar} alt=""/>
                  </div>
              </div>

              <div className="hole hole6">
                  <div className="avatar lg">
                  <img src={avatar} alt=""/>
                  </div>
              </div>
        </div>
            <div className="gameover-screen">
                <div className="gameover-screen__message">Game Over</div>
                <div className="gameover-screen__score">Score: {score}</div>
                <Link to="/select-avatar">
                <button className="back-btn" onClick={selectionScreen}>Play Again</button>
                </Link>
               
          </div>
          <Footer  score = {score}/>
      </div>
      </Route>
      </Switch>
         
         
    </div>
    
  );
}

export default App;
